function sliderWorksMenu() {
    var swiper = new Swiper('.works-menu .swiper', {
        slidesPerView: "auto",
        spaceBetween: 40,
        slideToClickedSlide: 'true',
        pagination: {
            el: ".works-menu .swiper-pagination",
            type: "progressbar",
        },
    })
}

function sliderProducts() {
    var swiper = new Swiper('.products .swiper', {
        slidesPerView: "5",
        spaceBetween: 32,
        slideToClickedSlide: 'true',
        navigation: {
            nextEl: '.products .swiper-button-next',
            prevEl: '.products .swiper-button-prev',
        },
        pagination: {
            el: ".products .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 2.3,
                spaceBetween: 12,
            },
            577: {
                slidesPerView: 3.3,
                spaceBetween: 16,
            },
            769: {
                slidesPerView: 4,
                spaceBetween: 20,
            },
            1201: {
                slidesPerView: 5,
                spaceBetween: 32,
            },
        }
    })
}

function sliderProducts2() {
    var swiper = new Swiper('.products2 .swiper', {
        slidesPerView: "4",
        spaceBetween: 32,
        slideToClickedSlide: 'true',
        navigation: {
            nextEl: '.products2 .swiper-button-next',
            prevEl: '.products2 .swiper-button-prev',
        },
        pagination: {
            el: ".products2 .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 1.8,
            },
            577: {
                slidesPerView: 2.3,
            },
            769: {
                slidesPerView: 3,
            },
            1201: {
                slidesPerView: 4,
            },
        }
    })
}

function sliderProducts3() {
    var swiper = new Swiper('.products3 .swiper', {
        slidesPerView: "4",
        spaceBetween: 32,
        navigation: {
            nextEl: '.products3 .swiper-button-next',
            prevEl: '.products3 .swiper-button-prev',
        },
        pagination: {
            el: ".products3 .swiper-pagination",
            clickable: "true",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 2.3,
            },
            769: {
                slidesPerView: 3,
            },
            1201: {
                slidesPerView: 4,
            },
        }
    })
}

$(document).ready(function() {
    sliderWorksMenu();
    sliderProducts();
    sliderProducts2();
    sliderProducts3();

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("active-tab")
        $(this).addClass("active-tab")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Accordion
    $(".accordion-show").click((function(){
        $(this).toggleClass("accordion-show--active")
        $(this).siblings(".accordion-hidden").slideToggle()
    }));


})